/* stylelint-disable scss/dollar-variable-empty-line-before */
/* stylelint-disable-next-line value-keyword-case */
/** Bulma: Variables **/
/** Main **/
#inputForm,
#playerContainer {
  margin-bottom: 1.5rem;
}

.video-js {
  margin: 0 auto;
}
.video-js.vjs-wavesurfer.vjs-fluid {
  /* stylelint-disable-next-line selector-type-no-unknown */
}
.video-js.vjs-wavesurfer.vjs-fluid wave.vjs-wavedisplay {
  height: calc(100% - 30px) !important;
}
.video-js .vjs-loop-button .vjs-icon-placeholder,
.video-js .vjs-seek-button .vjs-icon-placeholder {
  /* stylelint-disable-next-line font-family-no-missing-generic-family-keyword */
  font-family: fontello;
  font-weight: normal;
  font-style: normal;
  cursor: pointer;
}
.video-js .vjs-loop-button .vjs-icon-placeholder::before,
.video-js .vjs-seek-button .vjs-icon-placeholder::before {
  font-size: 1.5em;
  line-height: 1.97;
}
.video-js .vjs-loop-button:not(.vjs-loop-enabled) {
  color: hsl(0deg, 0%, 48%);
}
.video-js .vjs-loop-button .vjs-icon-placeholder::before {
  content: "\e81f";
}
.video-js .vjs-seek-button.skip-back .vjs-icon-placeholder::before {
  content: "\e81e";
  transform: none;
}
.video-js .vjs-seek-button.skip-forward .vjs-icon-placeholder::before {
  content: "\e818";
  transform: none;
}